// common/http.interceptor.js

const install = (Vue, vm) => {
	// 
	
	if (process.env.NODE_ENV === 'development') {
		var baseUrl = 'https://jzg.phpgzs.top/api';
	} else {
		var baseUrl = 'https://jzg.apphf.cn/api';
	}

	// 
	Vue.prototype.$u.http.setConfig({
		// ......
		baseUrl: baseUrl, // 
		// method: 'POST',
		// jsonJSON.parse()
		dataType: 'json',
		showLoading: true, // loading
		loadingText: '...', // loadingz
		loadingTime: 200, // ms
		originalData: false, // 
		loadingMask: true, // loading
		// 
		header: {
			// 'content-type': 'application/x-www-form-urlencoded;charset=utf-8;'
			'content-type': 'application/json;charset=UTF-8;'
		},
	});

	// 
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// ......
		config.header.token = uni.getStorageSync('token');
		return config;
	}

	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		if (res.error == 0) {
			// rescoderesult
			// res.resultthis.$u.post(url).then(res => {})thenres
			// originalDatatrue
			return res;
		} else if (res.error == 1002 || res.error == 1003) {
			uni.showToast({
				title: '',
				duration: 2000,
				icon: 'none'
			});
			uni.clearStorageSync();
			setTimeout(() => {
				// uView
				vm.$u.route('/pages/login/login')
			}, 1000)
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			return res;
		} else if (res.error == 1001) {
			uni.showToast({
				title: '',
				duration: 2000,
				icon: 'none'
			});
			uni.clearStorageSync();
			setTimeout(() => {
				// uView
				vm.$u.route('/pages/login/login')
			}, 1000)
		} else {
			// vm.$u.toast(res.msg);
			uni.showToast({
				title: res.msg,
				icon: 'none'
			})
			return res;
		}
	}
}

export default {
	install
}
